/* https://icomoon.io/app */

@font-face {
    font-family: "Poltergeist";
    font-weight: normal;
    font-style: normal;
    src:
        url("../fonts/Poltergeist.ttf?eijl9v") format("truetype"),
        url("../fonts/Poltergeist.woff?eijl9v") format("woff"),
        url("../fonts/Poltergeist.svg?eijl9v#Poltergeist") format("svg");
    font-display: block;
}

.icon::before {
    /* speak: never; */
    font: normal normal normal 24px/1 Wave, sans-serif;

    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "Poltergeist" !important;
    font-size: inherit;
    line-height: 1;
    font-feature-settings: "liga";
    font-feature-settings: "liga=1";
    font-feature-settings: "liga";
    font-feature-settings: "liga" 1;
    font-feature-settings: "liga";

    /* Enable Ligatures ================ */
    letter-spacing: 0;
    text-transform: none;
    font-variant-ligatures: discretionary-ligatures;
    font-variant-ligatures: discretionary-ligatures;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

